// Style 1
.nav {
	@include transition(all .8s);
	@include box-shadow(0 0 2px rgba(0,0,0,0.1));
	position: fixed;
	bottom: 0;
	left: 0;
	background: rgba(0,0,0, .7);
	width: 100%;
	border-top: 1px solid rgba(255, 255, 255, .2);
  	border-bottom: 1px solid rgba(255, 255, 255, .2);
	text-align: center;

	ul {
		list-style-type: none;
		margin: 0;
		padding: 0;
		text-align: center;
	}

	li {
		display: inline;
	}

	a {
		display: inline-block;
		height: 55px;
		line-height: 55px;
		margin: 0 30px;
		color: $white;
		font-size: 30px;
		font-weight: bold;
		font-family: myriad-pro, "Open Sans" Arial, Helvetica, sans-serif;
		text-decoration: none;
	}

	.active a,
	a:hover {
		color: #f8799b;
	}
}

