// Style 3
.sticky .nav {
	top: 0;
	left: auto;
	right: 0;
	background: transparent;

	float: right;
	width: auto;
	margin: 30px $side-gap 0 0;

	@include box-shadow(none);
	border-top: none;
	border-bottom: none;

	ul {
		list-style-type: none;
		margin: 0;
		padding: 0;
		text-align: center;
	}

	li {
		display: inline;
	}

	a {
		height: 35px;
		line-height: 35px;
		margin-left: 15px;
		margin-right: 15px;
	}
}